	module.exports = {"data":{"markdownRemark":{"html":"","frontmatter":{"title":"Trumpet","date":"April 13, 2018","path":"/trumpet","about":"‘Trumpet’ is a Node.js server that listens to tweets from NYC that contain the words “trump” and “protest” and plays a note for every tweet. I developed ‘Trumpet’ with Dror Ayalon during Spotify’s NYC Monthly Music Hackathon.\nSince the Hackathon took place on January 21st, 2017, a day after Donald Trump’s inauguration, we knew we wanted to create a generative music composition based on people emotions towards the president elect, and the protests around the inauguration.","cover":"trumpet.png","credits":"Developed with <a target=\"_blank\" href=\"http://drorayalon.com\">Dror Ayalon</a>","components":[["code","Javascript, cSound"],["software","Node.js, cSound Node Bindings"],["3d","Twitter API"]],"press":[],"links":[["Github","https://github.com/dodiku/trumpet"]],"embed":"<iframe width=\"100%\" height=\"450\" src=\"https://www.youtube.com/embed/4YlOzWwsXKo?rel=0&amp;controls=0&amp;showinfo=0\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","tags":["Experiment"],"excerpt":"The sound of a protest."}}},"pathContext":{"prev":{"html":"","id":"/home/travis/build/juniorxsound/portfolio/src/pages/projects/visualiser.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2018-04-14T22:12:03.284Z","path":"/visualizer","title":"Visualizer","links":[["Demo","https://juniorxsound.github.io/ICM-Fall-2016/3D_Web_Audio_Visualiser/"],["Github","https://github.com/juniorxsound/ICM-Fall-2016/tree/master/3D_Web_Audio_Visualiser"]],"credits":"","press":[],"components":[["code","Javascript, GLSL"],["software","Blender, e-on Vue, Adobe Photoshop"],["3d","Three.js"]],"cover":"visualizer.png","about":"‘Visualizer’ was born after an R&D process I did with Roey Tsemah and Ronen Tanchum. Initially our goal was to research how we could use the Web Audio API, to analyse incoming signal (music or microphone inputs), and drive geometry deformations in real-time, while keeping performance optimal so it could run on mobile browsers as well.\nAfter the R&D process was done, I went on to design the concept art for this visualizer, using 3D animation and environment design software packages and then implementing these designs in the web using Three.js. This work was made possible thanks to the amazing post “Experiments with Perlin Noise” examples by Jaume Sanchez.","embed":"","excerpt":"A 3D web audio sound visulaizer.","tags":["Experiment"]}},"next":{"html":"","id":"/home/travis/build/juniorxsound/portfolio/src/pages/projects/max.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2018-04-12T22:12:03.284Z","path":"/max","title":"DepthKit for Max/MSP","links":[["Github","https://github.com/juniorxsound/DepthKit-for-Max"]],"credits":"Developed with <a href=\"https://drorayalon.com\" target=\"_blank\">Dror Ayalon</a>.","press":[],"components":[["code","GLSL"],["software","DepthKit"],["3d","Jitter"]],"cover":"max.png","about":"DepthKit for Max is a sample Max patch demonstrating a workflow for playing volumetric videos in Max/Msp/Jitter using DepthKit combined-per-pixel exports.","embed":"","excerpt":"Render volumetric video in Max/MSP.","tags":["Tools"]}}}}

